import React, {Component} from "react";
import {connect} from "react-redux";
import ProductOfferPriceTooltipComponent from "eshop/modules/simfree/components/ProductOfferPriceTooltipComponent"
import OnlineUtils from "eshop/utils/OnlineUtils";
import {getPriceIsNet} from "eshop/modules/cart/selectors"
import {OraLoader, OraSelect} from "eshop/components/OraCommonComponents";
import {getContractRoleInProgress} from 'eshop/modules/configurator/selectors/offers';

var OOVMonthlyPriceTagCount = 0;

class OOVMonthlyPriceTag extends Component {
    constructor(props) {
        super(props)
        this.state = {countId: OOVMonthlyPriceTagCount++};
    }

    getPrice(prices) {
        return this.props.showNet ? (prices.priceNet || prices.originalNetPrice) : (prices.priceGross || prices.originalGrossPrice)

    }

    render() {

        let baseSummaryPrice = this.props.baseSummaryPrice;
        let convSummaryPrice = this.props.convSummaryPrice;
        let selected = this.props.selected;

        let monthlyPayments = this.props.monthlyPayments;
        let monthlyPaymentsWithBonuses = this.props.monthlyPaymentsWithBonuses;

        if (!this.props.isMagnum && monthlyPayments && monthlyPayments.length > 0) {

            baseSummaryPrice = this.getPrice(monthlyPayments[monthlyPayments.length - 1]);

        }
        if (!this.props.isMagnum && monthlyPaymentsWithBonuses.length > 0) {
            convSummaryPrice = this.getPrice(monthlyPaymentsWithBonuses[monthlyPaymentsWithBonuses.length - 1]);
        }
        let alignClass = this.props.alignClass || " u-right u-small-right ";

        return (<div>
                {(this.props.clientContext && convSummaryPrice != baseSummaryPrice) ?
                    <div className={alignClass}>
                        {(selected) &&
                        OnlineUtils.updateOgTag('product:original_price:offer_price', OnlineUtils.formatPrice(convSummaryPrice))
                        }
                        {(selected) &&
                        OnlineUtils.updateOgTag('product:offer_loyalyty', monthlyPaymentsWithBonuses && monthlyPaymentsWithBonuses[monthlyPaymentsWithBonuses.length - 1] && monthlyPaymentsWithBonuses[monthlyPaymentsWithBonuses.length - 1].monthTo)
                        }
                        <ProductOfferPriceTooltipComponent
                            key={"client_" + this.props.clientContext + "_" + this.props.showNet + "_" + this.props.baseId}
                            offerDiscountInfoDescription={this.props.offerDiscountInfoDescription}
                            offerDiscountInfoConvDescription={this.props.offerDiscountInfoConvDescription}
                            monthlyPayments={monthlyPayments}
                            monthlyPaymentsWithBonuses={monthlyPaymentsWithBonuses}
                            clientContext={this.props.clientContext}
                            loyaltyLength={this.props.loyaltyLength}
                            baseId={this.props.baseId}
                        >

                            <span className="u-left u-small-left">
                                <OraLoader loading={this.props.getContractRoleInProgress}
                                           size="s"
                                           id={"contract-role-in-progress-offers-loader-" + this.state.countId + OOVMonthlyPriceTagCount}
                                           parentId={"offer-filter-loader-opaque"}
                                           blockType={"span"}

                                >
                                    <span
                                        className={this.props.mainPriceClass + " u-font-bold"}>{OnlineUtils.formatPrice(convSummaryPrice)}&nbsp;zł{this.props.monthString && ("/" + this.props.monthString)}</span>
                            </OraLoader>
                            <br/>

                                <OldPrice baseSummaryPrice={baseSummaryPrice} monthString={this.props.monthString}/>

                        </span>

                        </ProductOfferPriceTooltipComponent>


                    </div>
                    :
                    <div className={alignClass}>
                        {(selected) &&
                        OnlineUtils.updateOgTag('product:original_price:offer_price', OnlineUtils.formatPrice(baseSummaryPrice))}
                        {(selected) &&
                        OnlineUtils.updateOgTag('product:offer_loyalyty', monthlyPayments && monthlyPayments[monthlyPayments.length - 1] && monthlyPayments[monthlyPayments.length - 1].monthTo)
                        }

                        <ProductOfferPriceTooltipComponent
                            key={"client_" + this.props.clientContext + "_" + this.props.showNet + "_" + this.props.baseId+this.state.countId}
                            offerDiscountInfoDescription={this.props.offerDiscountInfoDescription}
                            offerDiscountInfoConvDescription={this.props.offerDiscountInfoConvDescription}
                            monthlyPayments={monthlyPayments}
                            monthlyPaymentsWithBonuses={monthlyPaymentsWithBonuses}
                            loyaltyLength={this.props.loyaltyLength}
                            clientContext={this.props.clientContext}
                            baseId={this.props.baseId}
                        ><span className="u-left u-small-left">
                                 <OraLoader loading={this.props.getContractRoleInProgress}
                                            size="s"
                                            id={"contract-role-in-progress-offers-loader-" + this.state.countId + OOVMonthlyPriceTagCount}
                                            parentId={"offer-filter-loader-opaque"}
                                            blockType={"span"}

                                 >
                                    <span
                                        className={this.props.mainPriceClass + " u-font-bold"}>{OnlineUtils.formatPrice(baseSummaryPrice)}&nbsp;zł{this.props.monthString && ("/" + this.props.monthString)}</span>
                                 </OraLoader>
                                </span>
                        </ProductOfferPriceTooltipComponent>

                    </div>
                }
            </div>
        )
    }
}


const OldPrice = (props) => {

    return (
        <span
            className="g-gray5-c u-font-bold  u-text-line-through u-left u-small-left">{OnlineUtils.formatPrice(props.baseSummaryPrice)}&nbsp;zł{props.monthString && ("/" + props.monthString)}</span>
    )

}

const mapStateToProps = state => ({
    showNet: getPriceIsNet(state),
    getContractRoleInProgress: getContractRoleInProgress(state)
})
export default connect(mapStateToProps, null)(OOVMonthlyPriceTag)

